}
.modal {
	position: absolute;
	width: 627px;
	height: 375px;
	left: 50%;
  	top: 50%;
	transform: translate(-50%,-50%);
}
.modal-background {
	position: absolute;
	background-color: #0B2740;
	opacity: 0.7;
	width: 100%;
	height: 100%;
	border: 5px solid #1B3B6F;
	transform: matrix(1, 0, 0, -1, 0, 0);
	border-radius: 45px;
	z-index: -1;
}
.modal-content {
	width: 100%;
	display: flex;
	flex-direction: column;
	justify-content: space-evenly;
	align-items: center;
	height: 100%;
	position: absolute;
}
.modal-text {
	width: 569px;
	font: bold 24px "Roboto";
	text-align: center;
	color: white;
	line-height: 28px;
	font-weight: 900;
}
.trade-btn {
	width: 264px;
	height: 63px;
	text-align: center;
	color: white;
	font-weight: 900;
	font: bold 24px "Roboto";
	border: 1px solid #000000;
	box-sizing: border-box;
	box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
	border-radius: 20px;
	background-color:#142C52;
	
}